#container {
    width: 360px;
    height: 640px;
    border-style: solid;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: 50px 1fr;
    background: lightgoldenrodyellow;
}

h2 {
    font-family: Georgia;
}

.emptyCheckbox {
    visibility: hidden;
}

ul {
    margin-left: -40px;
    position: absolute;
    /*left: 50px;*/
    grid-column: 1/span 2
}

li {
    padding-left: 40px;
    margin-right: -129px;
}

.linespan {
    display: inline;
}

#title {
    text-align: center;
    grid-row: 1/span all;
    grid-column: 2/span 1;
    margin: 0;
    border-left: solid red;
    padding-top: 13px;
    padding-bottom: 0;
}

.back-button {
    background-color: aliceblue;
    width: 50px;
    height: 50px;
    font-size: 30px;
    border-style: solid;
    border-radius: 100px;
    font-size: 25px;
    grid-row: 1/span 1;
    grid-column: 1/span 1;
}

